@font-face {
  font-family: Cricket-Bold;
  /* Гарнитура шрифта */
  src: local(Cricket-Bold), url(../fonts/cricket-bold_45ed3a464621b3dcc182763b550be2c5.woff);
  /* Для остальных браузеров */
}

body {
  font-family: "Calibri";
  line-height: 1.5;
  font-size: 87.5%;
  margin: 0;
  padding: 0;
  color: #666;
}

ul {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 25px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  font-family: Calibri;
  text-shadow: 1px 1px 1px #000;
}

h2 {
  font-family: Cricket-Bold;
  text-transform: uppercase;
  font-size: 35px;
  font-weight: 500;
}

header {
  background: url(../images/1.jpg);
  background-size: cover;
  background-position-x: -224px;
  color: #fff;
}

header a {
  color: #fff;
}

header h2 {
  display: inline-block;
  margin: 5px auto 0px;
  background-color: #a3d063;
  border-radius: 30px;
  line-height: 40px;
  color: #ffffff;
  font-size: 30px;
  font-weight: normal;
  text-align: center;
  font-style: italic;
  padding-left: 15px;
  padding-right: 15px;
  text-shadow: 2px 3px 0px #769748;
}

header article:last-child div:first-child {
  flex: 3 1 300px;
}

.head {
  max-width: 100%;
}

.head article {
  background: inherit;
}

.head article:first-child div:first-child {
  flex: 0 1 100px;
}

.head article:first-child div:last-child {
  flex: 0 1 300px;
}

.head .menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.head .menu li {
  display: inline-block;
}

#logo {
  width: 80px;
  height: 80px;
}

.header-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: flex-end;
}

.header-wrapper div {
  flex: 0 0 0 !important;
}

.header-text {
  margin-right: 125px;
  margin-bottom: 60px;
  color: #000;
}

.header-text div:last-child {
  background: #fff;
  text-align: left;
  padding: 20px;
}

.header-text div:first-child {
  margin-bottom: 50px;
  padding: 5px;
  border: 2px dashed #a3d063;
  border-radius: 4px;
  margin-top: 60px;
  width: 202px;
}

.header-text div:first-child span {
  border-radius: 3px;
  background-color: #a3d063;
  color: #ffffff;
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  line-height: 60px;
  width: 100%;
  display: block;
  position: relative;
  text-shadow: 2px 3px 2px #82a64f;
}

.header-text span {
  color: #FFA500;
  font-weight: bold;
}

.header-text > div {
  width: 300px;
  flex: auto !important;
}

main section {
  padding: 50px 0;
}

main section span {
  color: #ffffff;
  background-color: #a3d063;
  padding-left: 2px;
  padding-right: 2px;
  border-radius: 3px;
}

main .adress {
  padding: 10px;
}

main .adress div {
  width: 100%;
}

main .adress div img {
  float: left;
  padding-right: 10px;
}

.d-2 {
  background: #fafafa;
}

.d-4 {
  background: url(../images/5.jpg);
  background-size: cover;
  padding: 50px 0 100px 0;
  position: relative;
}

.d-4::before {
  content: '';
  width: 50px;
  height: 50px;
  background: #fff;
  position: absolute;
  top: -25px;
  left: 50%;
  transform: rotate(45deg);
}

.d-5 {
  background: #444;
  color: #fff;
}

.d-6 {
  background-color: #a3d063;
  color: #fff;
}

.d-6 article span {
  font-family: "Calibri";
  font-size: 25px;
  display: block;
  padding: 0;
  text-transform: lowercase;
}

.d-6 article a {
  font-size: 35px;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  border-bottom: 2px solid;
  padding: 10px;
}

.d-6 input[type="text"] {
  border: 1px solid #ffffff;
  border-radius: 3px;
  padding: 5px 15px;
  margin: 0px 20px 10px auto;
}

.d-6 button[type="submit"] {
  border: medium none;
  border-radius: 3px;
  background-color: #26a7ea;
  height: 35px;
  text-align: center;
  width: 100px;
  color: #ffffff;
  font-size: 14px;
  cursor: pointer;
  text-shadow: 2px 2px 2px #208cc4;
  box-shadow: 0px -3px 0px rgba(0, 0, 0, 0.2) inset;
}

.d-6 button[type="submit"]:hover {
  box-shadow: 0px -3px 0px rgba(0, 0, 0, 0.2) inset;
  background-color: #ffa200;
  text-shadow: 2px 2px 2px #d88900;
}

.d-7 div {
  position: relative;
}

.d-7 div::after {
  content: '';
  width: 67px;
  height: 57px;
  background: url(../images/9.jpg) no-repeat;
  position: absolute;
  top: 20%;
  right: 0;
}

.d-7 div:last-child::after {
  content: '';
  width: 67px;
  height: 57px;
  background: none;
  position: absolute;
  top: 20%;
  right: 0;
}

.d-8 div {
  flex: 4 1 200px;
  padding: 10px;
}

.d-8-1 {
  background: url(../images/10.jpg);
  background-size: cover;
}

.d-8-1 div {
  position: relative;
}

.d-8-1 div::after {
  content: '';
  width: 32px;
  height: 50px;
  background: url(../images/14.png);
  position: absolute;
  top: 30%;
  right: 0;
}

.d-8-1 div:last-child::after {
  content: '';
  width: 67px;
  height: 57px;
  background: none;
  position: absolute;
  top: 20%;
  right: 0;
}

.d-9 {
  background: url(../images/16.jpg);
}

.d-9 div {
  padding: 10px;
}

.d-9 div h3 {
  margin: 0 auto;
  border-radius: 30px;
  padding: 10px;
}

.d-9 div:first-child h3 {
  background: #fff;
  width: 70%;
}

.d-9 div:first-child p {
  text-align: left;
}

.d-9 div:first-child p::before {
  background: url(../images/19.png);
}

.d-9 div:last-child h3 {
  background: #a3d063;
  width: 60%;
  color: #fff;
}

.d-9 div:last-child p {
  text-align: left;
}

.d-9 div:last-child p::before {
  background: url(../images/20.png);
}

.d-9 p {
  position: relative;
  padding-left: 20px;
}

.d-9 p::before {
  content: '';
  width: 24px;
  height: 24px;
  position: absolute;
  left: -8px;
}

.d-10 .article-title {
  justify-content: center;
}

.d-10 article {
  justify-content: flex-start;
}

.d-10 article div {
  flex: none;
}

.d-10 article .left {
  margin-right: 20px;
}

.d-10 article .left img {
  border-radius: 50%;
}

.d-10 article .right {
  width: 80%;
  text-align: left;
  background-image: url('../images/17.jpg');
  background-repeat: no-repeat;
  background-position: left top;
  padding-left: 20px;
}

.d-10 article .right p {
  font-size: 18px;
}

.d-10 article .right div {
  font-size: 20px;
}

.d-10 article .right div span {
  font-size: 30px;
  font-weight: bold;
  color: #444444;
}

.d-10 article .right span {
  color: #999999;
  font-size: 15px;
  font-style: italic;
  background: none;
}

.d-11 {
  background-color: #f4f4f4;
}

.d-12 {
  background: url(../images/24.jpg) no-repeat;
  background-position-x: 50%;
}

.d-12 article:last-child {
  justify-content: flex-end;
}

.d-12 article div {
  flex: none;
  max-width: 250px;
  width: 100%;
}

.warn {
  position: relative;
  padding-left: 60px;
  margin: 50px 0;
  text-align: left;
}

.warn::before {
  content: '';
  width: 54px;
  height: 47px;
  position: absolute;
  background: url(../images/11.jpg);
  left: 0;
  top: 9px;
}

.price-table table {
  border-collapse: collapse;
  width: 100%;
}

.table-head th {
  background: none;
}

.table-head td {
  font-weight: bold;
}

.table-head td:last-child {
  color: #fff;
  background-color: #a3d063;
}

.price-table table th,
.price-table table td {
  border: 1px solid #a3d063;
  background: #fff;
  padding: 10px 0;
  font-size: 18px;
  text-align: center;
}

.price-table th {
  text-align: center;
}

.price-table .table-body th {
  padding: 10px 0 10px 25px;
  text-align: left;
}

.price-table .atypical {
  color: #fff;
  background-color: #a3d063;
}

.price-desc {
  position: relative;
  padding: 10px;
  text-align: left;
  color: #fff;
  background-color: #a3d063;
}

.price-desc::after {
  content: '';
  position: absolute;
  right: 68px;
  top: -27px;
  border: 15px solid transparent;
  border-bottom: 15px solid #a3d063;
}

.price-desc li {
  font-size: 17px;
  line-height: 30px;
  list-style: none;
}

.price-desc-right {
  float: right;
  position: relative;
}

.price-desc-right > span {
  display: block;
  height: 75px;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 2;
  margin-top: 40px;
}

.price-desc-right > span > span {
  display: block;
  padding: 10px;
  text-align: center;
  font-size: 26px;
  color: #ffffff;
  background-color: #a3d063;
  font-weight: bold;
  text-shadow: 2px 2px 2px #6b8841;
}

.price-desc-right > div {
  background-color: #ffffff;
  overflow: hidden;
  border-radius: 3px;
  padding: 100px 15px 35px;
  color: #444444;
  font-size: 22px;
  margin-left: 11px;
  margin-top: 25px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}

.block-board {
  position: relative;
  padding: 0 10px;
  margin: 0 50px;
}

.block-board > div {
  padding: 10px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}

.block-board h3 {
  background: #a3d063;
  color: #fff;
  position: absolute;
  left: 0;
  width: 100%;
  padding: 10px 0;
}

.block-board h3::after {
  content: '';
  width: 10px;
  height: 10px;
  position: absolute;
  right: 0;
  top: -10px;
  background: url(../images/11.png);
}

.block-board h3::before {
  content: '';
  width: 10px;
  height: 10px;
  position: absolute;
  left: 0;
  bottom: -10px;
  background: url(../images/12.png);
}

.block-board p {
  padding-top: 60px;
}

article {
  text-align: center;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

article div {
  flex: 1 1 300px;
}

footer {
  color: #fff;
  background-color: #444444;
}

footer a {
  color: #fff;
}

footer article {
  background: inherit;
  align-items: center;
}

footer article .contact-info {
  text-align: right;
}

.adress {
  text-align: left;
}

.adress div {
  display: inline-block;
  vertical-align: top;
}

.contact-info-wrapper,
.contact-info #block-tel,
.menu-site nav,
.footer-layout {
  padding-left: 30px;
  padding-right: 30px;
}

.div-t-form {
  width: 300px;
  border-width: 3px;
  border-style: solid;
  border-color: #a3d063 #a3d063 #ffffff;
  -moz-border-top-colors: none;
  -moz-border-right-colors: none;
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  border-image: none;
  background-color: #ffffff;
  border-radius: 5px 5px 0px 0px;
  padding-top: 30px;
  padding-bottom: 20px;
  margin-top: 15px;
}

.div-t-form .div-for-top {
  text-align: center;
  padding-left: 10px;
  text-transform: uppercase;
  color: #444444;
  font-size: 20px;
  font-weight: bold;
  padding-right: 10px;
}

.div-t-form .div-for-top span {
  display: block;
  margin-top: 10px;
  margin-bottom: 20px;
  color: #92bb59;
  font-size: 16px;
}

.div-t-form > form {
  display: block;
  height: 160px;
  margin-top: 10px;
}

.div-t-form > form input[type="text"] {
  display: block;
  height: 33px;
  padding-left: 12px;
  padding-right: 12px;
  margin: 0px auto 20px;
  width: 184px;
  border: 1px solid #444444;
  border-radius: 3px;
}

.div-t-form > form input[type="text"]:focus {
  border: 1px solid #7ba83b;
  box-shadow: 0px 0px 5px 0px #8dc044;
}

.div-t-form > form button[type="submit"] {
  border: medium none;
  border-radius: 3px;
  background-color: #26a7ea;
  height: 35px;
  text-align: center;
  width: 100px;
  color: #ffffff;
  font-size: 14px;
  margin: 0px auto;
  display: block;
  cursor: pointer;
  text-shadow: 2px 2px 2px #208cc4;
  box-shadow: 0px -3px 0px rgba(0, 0, 0, 0.2) inset;
}

.div-t-form > form button[type="submit"]:hover {
  box-shadow: 0px -3px 0px rgba(0, 0, 0, 0.2) inset;
  background-color: #ffa200;
  text-shadow: 2px 2px 2px #d88900;
}

.div-t-form > form button[type="submit"]:active {
  box-shadow: 0px 3px 0px rgba(0, 0, 0, 0.2) inset;
  background-color: #424140;
  text-shadow: 2px 2px 2px #424140;
}

.div-t-form .div-for-butt {
  color: #444444;
  font-size: 12px;
  text-align: center;
}

@media screen and (max-width: 768px) and (min-width: 320px) {
  .header-wrapper {
    justify-content: center;
    align-items: center;
  }

  .header-text {
    margin-right: 0;
  }

  .block-board {
    margin: 10px;
  }

  .d-7 div,
  .d-8 div {
    margin-bottom: 50px;
  }

  .d-7 div::after,
  .d-8 div::after {
    background: none;
  }

  .d-10 article {
    justify-content: center;
    margin: 50px 0;
  }
}